.book-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
}

.book-title {
  margin: 10px;
  text-align: center;
  color: rgba(159, 58, 58, 1);
}

.book-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.book-img {
  padding: 10px;
  max-width: 100%;
  /* background: #999; */
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.book-more {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 10px;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.book-subtext {
  margin: 0;
  font-weight: 400;
  padding: 10px;
}

.book-desc {

}

.categorie {
  display: inline-block;
  padding: 5px 10px;
  font-size: .9em;
  background-color: rgba(159, 58, 58, 1);
  color: #fff;
}

.expanded {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.btn {
  display: inline-block;
  padding: 10px 15px;
  font-weight: 600;
  border: 2px solid rgba(159, 58, 58, 1);
  color: rgba(159, 58, 58,1);
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

.btn:hover {
  cursor: pointer;
  background-color: rgba(159, 58, 58, 1);
  color:#fff;
  border-radius: 0px;
}

@media (max-width: 600px) {
  .book-info {
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .book-img {
    max-width: 100%;
    -webkit-align-self: center;
            align-self: center;
  }

  .book-desc {
    -webkit-align-self: flex-start;
            align-self: flex-start;
  }

  .categorie {

  }
}

.notavail {
  height: 200px;
  width: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
}

.notavail h3 {
  margin: 0 auto;
  padding: 1em;
  color: #444;
  text-transform: uppercase;
  text-align: center;
  box-shadow: inset 0 0 50px 10px rgba(0,0,0,.1);
